<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getUserList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getUserList"
            ></el-button> </el-input
        ></el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="usersList" border stripe>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="name"></el-table-column>
        <el-table-column label="" prop="college"></el-table-column>
        <el-table-column label="" prop="class"></el-table-column>
        <el-table-column label="" prop="telephone"></el-table-column>
        <el-table-column label="" prop="title"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tooltip
              effect="dark"
              content=""
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="showEditDialog(scope.row.number)"
              ></el-button>
            </el-tooltip>
            <el-tooltip
              effect="dark"
              content=""
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="removeUserByNumber(scope.row.number)"
              ></el-button>
            </el-tooltip>
            <el-tooltip
              effect="dark"
              content=""
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="changeUserByNumber(scope.row.number)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pageNum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="30%"
      @close="addDialogClosed"
    >
      <!--  -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="college">
          <el-input v-model="addForm.college"></el-input>
        </el-form-item>
        <el-form-item label="" prop="class">
          <el-input v-model="addForm.class"></el-input>
        </el-form-item>
        <el-form-item label="" prop="telephone">
          <el-input v-model="addForm.telephone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-input v-model="addForm.title"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addUser"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <!--  -->
      <el-form
        ref="editFormRef"
        :model="editForm"
        :rules="editFormRules"
        label-width="100px"
      >
        <el-form-item label="" prop="number">
          <el-input v-model="editForm.number" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="editForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="college">
          <el-input v-model="editForm.college"></el-input>
        </el-form-item>
        <el-form-item label="" prop="class">
          <el-input v-model="editForm.class"></el-input>
        </el-form-item>
        <el-form-item label="" prop="telephone">
          <el-input v-model="editForm.telephone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-input v-model="editForm.title"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo()"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="changeDialogVisible" width="30%">
      <el-form ref="changeFormRef" :model="changeForm" label-width="80px">
        <el-form-item label="" style="width: 50%">
          <el-input v-model="changeForm.name" disabled></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="changeForm.status" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="changeDialogVisible = false"> </el-button>
        <el-button type="primary" @click="handleChange"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  created() {
    this.getUserList();
  },
  name: "UserFirst",
  data() {
    return {
      // 
      queryInfo: {
        query: "",
        // 
        pageNum: 1,
        // 
        pageSize: 2,
      },
      usersList: [],
      total: 0,
      // /
      addDialogVisible: false,
      // 
      addForm: {
        name: "",
        college: "",
        class: "",
        telephone: "",
        title: "",
      },
      // 
      addFormRules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 4, message: "2~4", trigger: "blur" },
        ],
        college: [{ required: true, message: "", trigger: "blur" }],
        class: [{ required: true, message: "", trigger: "blur" }],
        telephone: [{ required: true, message: "", trigger: "blur" }],
        title: [{ required: true, message: "", trigger: "blur" }],
      },
      // /
      editDialogVisible: false,
      // 
      editForm: {
        name: "",
        college: "",
        class: "",
        telephone: "",
        title: "",
      },
      editFormRules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 4, message: "2~4", trigger: "blur" },
        ],
        college: [{ required: true, message: "", trigger: "blur" }],
        class: [{ required: true, message: "", trigger: "blur" }],
        telephone: [{ required: true, message: "", trigger: "blur" }],
        title: [{ required: true, message: "", trigger: "blur" }],
      },
      // 
      changeDialogVisible: false,
      changeForm: {},
    };
  },
  methods: {
    async getUserList() {
      const { data: res } = await this.$http.get("/Home/userFirst", {
        params: this.queryInfo,
      });
      if (res.meta.status !== 200)
        return this.$message.error("");
      this.usersList = res.data;
      this.total = res.total;
    },
    // pageSize
    handleSizeChange(newSize) {
      console.log(newSize);
      this.queryInfo.pageSize = newSize;
      this.getUserList();
      console.log(this.queryInfo.pageSize);
    },
    // pageNum
    handleCurrentChange(newPage) {
      console.log(newPage);
      this.queryInfo.pageNum = newPage;
      this.getUserList();
    },
    addDialogClosed() {
      this.$refs.addFormRef.resetFields();
    },
    // 
    addUser() {
      this.$refs.addFormRef.validate(async (valid) => {
        if (!valid) return;
        // 
        const { data: res } = await this.$http.post(
          "/Home/userFirst",
          this.addForm
        );
        if (res.meta.status !== 201) return this.$message.error("");
        this.$message.success("");
        this.addDialogVisible = false;
        this.getUserList();
      });
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
    },
    // 
    async showEditDialog(number) {
      this.editDialogVisible = true;
      const { data: res } = await this.$http.get("/Home/userFirst/" + number);
      if (!res.meta.status == 200)
        return this.$message.error("");
      this.editForm = res.data[0];
    },
    // 
    editUserInfo() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return;
        // 
        const { data: res } = await this.$http.put(
          "/Home/userFirst/" + this.editForm.number,
          {
            number: this.editForm.number,
            name: this.editForm.name,
            college: this.editForm.college,
            class: this.editForm.class,
            telephone: this.editForm.telephone,
            title: this.editForm.title,
          }
        );
        if (res.meta.status !== 200)
          return this.$message.error("");
        this.$message.success("");
      });
      this.editDialogVisible = false;
      this.getUserList();
    },
    // 
    async removeUserByNumber(number) {
      // 
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      const { data: res } = await this.$http.delete(
        "/Home/userFirst/" + number
      );
      if (res.meta.status !== 200) return this.$message.error("");
      this.$message.success("");
      this.getUserList();
    },
    async changeUserByNumber(number) {
      this.changeDialogVisible = true;
      const { data: res } = await this.$http.get("/Home/userFirst/" + number);
      if (res.meta.status !== 200) return this.$message.error("");
      this.$message.success("");
      this.changeForm = res.data[0];
    },
    async handleChange() {
      console.log(this.changeForm.number,this.changeForm.status);
      const { data: res } = await this.$http.put(
        "/Home/userFirst/up" ,{
          number:this.changeForm.number,
          status:this.changeForm.status
        }
      );
      if (res.meta.status !== 200) return this.$message.error("");
      this.$message.success("");
      window.sessionStorage.setItem('status',this.changeForm.status)
      this.changeDialogVisible = false;
    },
  },
};
</script>

<style scoped></style>
