<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card class="box-card">
      <el-upload
        class="upload-demo"
        ref="upload"
        :action="actionUrl"
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :on-success="handleSuccess"
        :on-error="handleError"
        :file-list="fileList"
        :auto-upload="false"
        :headers="herderObject"
        accept=".pdf"
        :limit="1"
      >
        <el-button slot="trigger" size="small" type="primary"
          ></el-button
        >
        <el-button
          style="margin-left: 10px"
          size="small"
          type="success"
          @click="submitUpload"
          ></el-button
        >
        <div slot="tip" class="el-upload__tip">
          pdf
        </div>
      </el-upload>
      <!--  -->
      <el-table :data="statesList" border stripe>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="pdfName"></el-table-column>
        <el-table-column label="" prop="time"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tooltip
              effect="dark"
              content=""
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="showEditDialog(scope.row.id)"
              ></el-button>
            </el-tooltip>
            <el-tooltip
              effect="dark"
              content=""
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="removeRequestById(scope.row.id)"
              >
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pageNum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <!--  -->
      <el-form
        ref="editFormRef"
        :model="editForm"
        :rules="editFormRules"
        label-width="100px"
      >
        <el-form-item label="" prop="id">
          <el-input v-model="editForm.id" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="pdfName">
          <el-input v-model="editForm.pdfName"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  created() {
    this.getStateList();
  },
  name: "StateSecond",
  data() {
    return {
      actionUrl: "http://127.0.0.1:5001/upload",
      fileList: [],
      herderObject: {
        Authorization: window.sessionStorage.getItem("token"),
      },
      // 
      queryInfo: {
        query: "",
        // 
        pageNum: 1,
        // 
        pageSize: 10,
      },
      statesList: [],
      total: 0,
      // /
      editDialogVisible: false,
      // 
      editForm: {
        id: "",
        pdfName: "",
      },
      editFormRules: {
        id: [{ required: true, message: "", trigger: "blur" }],
        pdfName: [{ required: true, message: "", trigger: "blur" }],
      },
    };
  },
  methods: {
    handleRemove(file, fileList) {
      console.log(file, "@@", fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.error(
        ` 1  ${files.length}  ${
          files.length + fileList.length
        } `
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(` ${file.name}`);
    },
    handleSuccess(response, file, fileList) {
      this.$message.success("");
      console.log(response, file, fileList);
    },
    handleError(err, file, fileList) {
      console.log("");
    },
    submitUpload() {
      this.$refs.upload.submit();
    },
    // 
    // pageSize
    async getStateList() {
      const { data: res } = await this.$http.get("/Home/stateSecond", {
        params: this.queryInfo,
      });
      if (res.meta.status !== 200)
        return this.$message.error("");
      this.statesList = res.data;
      this.total = res.total;
    },
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize;
      this.getStateList();
    },
    // pageNum
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage;
      this.getStateList();
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
    },
    // 
    async showEditDialog(id) {
      this.editDialogVisible = true;
      const { data: res } = await this.$http.get("/Home/stateSecond/" + id);
      if (!res.meta.status == 200)
        return this.$message.error("");
      this.editForm = res.data[0];
    },
    // 
    editUserInfo() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return;
        // 
        const { data: res } = await this.$http.put(
          "/Home/stateSecond/" + this.editForm.id,
          {
            pdfName: this.editForm.pdfName,
          }
        );
        if (res.meta.status !== 200)
          return this.$message.error("");
        this.$message.success("");
      });
      this.editDialogVisible = false;
      this.getStateList();
    },
    // 
    async removeRequestById(id) {
      // 
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      const { data: res } = await this.$http.delete("/Home/stateSecond/" + id);
      console.log(res);
      if (res.meta.status !== 200) return this.$message.error("");
      this.$message.success("");
      this.getStateList();
    },
  },
};
</script>

<style></style>
