<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-table :data="statesList" border stripe>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="pdfName"></el-table-column>
        <el-table-column label="" prop="time"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tooltip
              effect="dark"
              content=""
              placement="top-start"
              :enterable="false"
            >
              <a :href="url + scope.row.filename" target="_blank">
                <el-button
                  type="primary"
                  icon="el-icon-reading"
                  size="mini"
                ></el-button
              ></a>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pageNum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  created() {
    this.getStateList();
  },
  name: "StateFirst",
  data() {
    return {
      // 
      queryInfo: {
        query: "",
        // 
        pageNum: 1,
        // 
        pageSize: 10,
      },
      statesList: [],
      total: 0,
      url: process.env.VUE_APP_UPLOAD_URL,
    };
  },
  methods: {
    // 
    // pageSize
    async getStateList() {
      const { data: res } = await this.$http.get("/Home/stateSecond", {
        params: this.queryInfo,
      });
      if (res.meta.status !== 200)
        return this.$message.error("");
      this.statesList = res.data;
      this.total = res.total;
    },
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize;
      this.getStateList();
    },
    // pageNum
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage;
      this.getStateList();
    },
  },
};
</script>

<style></style>
