<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card class="box-card">
      <el-table :data="ForumList" style="width: 100%" max-height="800">
        <el-table-column fixed label="#" type="index" width="150">
        </el-table-column>
        <el-table-column prop="time" label="" width="200">
        </el-table-column>
        <el-table-column prop="name" label="" width="120">
        </el-table-column>
        <el-table-column prop="article" label="" width="300">
        </el-table-column>
        <el-table-column prop="pic1" label="1" width="200">
          <template slot-scope="scope">
            <img :src="img1 + scope.row.pic1" alt="" style="width: 150px" />
          </template>
        </el-table-column>
        <el-table-column prop="pic2" label="2" width="200">
          <template slot-scope="scope">
            <img :src="img2 + scope.row.pic2" alt="" style="width: 150px" />
          </template>
        </el-table-column>
        <el-table-column prop="pic3" label="3" width="200">
          <template slot-scope="scope">
            <img :src="img3 + scope.row.pic3" alt="" style="width: 150px" />
          </template>
        </el-table-column>
        <el-table-column prop="pic4" label="4" width="200">
          <template slot-scope="scope">
            <img :src="img4 + scope.row.pic4" alt="" style="width: 150px" />
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="" width="300">
          <template slot-scope="scope">
            <el-button
              @click="handleRemove(scope.row.idforum)"
              type="danger"
              size="small"
            >
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  created() {
    this.getForum();
  },
  name: "ForumSecond",
  data() {
    return {
      ForumList: [],
      img1: process.env.VUE_APP_UPLOAD_URL3,
      img2: process.env.VUE_APP_UPLOAD_URL4,
      img3: process.env.VUE_APP_UPLOAD_URL5,
      img4: process.env.VUE_APP_UPLOAD_URL6,
    };
  },
  methods: {
    async getForum() {
      const { data: res } = await this.$http.get("/Home/ForumSecond");
      if (res.meta.status !== 200) {
        this.$message.error("");
      } else {
        this.ForumList = res.data;
        this.$message.success("");
      }
    },
    async handleRemove(idforum){
      console.log(idforum);
            // 
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      const { data :res} = await this.$http.delete("/Home/ForumSecond/"+idforum)
      if (res.meta.status !== 200) return this.$message.error("");
      this.$message.success("");
      this.getForum();
    }
  },
};
</script>

<style scoped></style>
