<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!-- -->
      <el-form
        ref="formRef"
        :model="form"
        :rules="formRules"
        label-width="80px"
      >
        <el-form-item label="" prop="name" required>
          <el-input v-model="form.name" style="width: 40%"></el-input>
        </el-form-item>
        <el-form-item label="" prop="user" required>
          <el-input v-model="form.user" style="width: 40%"></el-input>
        </el-form-item>
        <el-form-item label="" prop="telephone" required>
          <el-input v-model="form.telephone" style="width: 40%"></el-input>
        </el-form-item>
        <el-form-item label="" prop="region" required>
          <el-select v-model="form.region" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" required>
          <el-col :span="11">
            <el-form-item prop="date1">
              <el-date-picker
                type="date"
                placeholder=""
                v-model="form.date1"
                value-format="yyyy-MM-dd"
                style="width: 90%"
              ></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col class="line date_w" :span="2">-</el-col>
          <el-col :span="11">
            <el-form-item prop="date2">
              <el-time-picker
                placeholder=""
                v-model="form.date2"
                value-format="HH:mm:ss"
                style="width: 90%"
              ></el-time-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="type" required>
          <el-radio-group v-model="form.type">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="introduction" required>
          <el-input
            type="textarea"
            v-model="form.introduction"
            style="width: 40%"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "RequestFirst",
  data() {
    return {
      form: {
        name: "",
        user: "",
        telephone: "",
        region: "",
        date1: "",
        date2: "",
        type: "",
        introduction: "",
      },
      formRules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        user: [{ required: true, message: "", trigger: "blur" }],
        telephone: [
          { required: true, message: "", trigger: "blur" },
        ],
        region: [
          { required: true, message: "", trigger: "blur" },
        ],
        date1: [{ required: true, message: "", trigger: "blur" }],
        date2: [{ required: true, message: "", trigger: "blur" }],
        type: [{ required: true, message: "", trigger: "blur" }],
        introduction: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    async onSubmit() {
      // 
      const confirmResult = await this.$confirm(
        "?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
       let number = window.sessionStorage.getItem("number");
      if (confirmResult == "confirm") {
        const { data: res } = await this.$http.post(
          "/Home/requestFirst",
          {form:this.form,number}
        );
        if (res.meta.status !== 200) {
          return this.$message.error("");
        }
        this.$message.success("");
        this.$refs.formRef.resetFields();
      }
    },
  },
};
</script>

<style scoped>
.el-col {
  width: 130px;
}
.date_w {
  width: 20px;
}
</style>
