<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getRequestList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="getRequestList"
            ></el-button> </el-input
        ></el-col>
      </el-row>
      <!--  -->
      <el-table :data="requestList" border stripe>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="name"></el-table-column>
        <el-table-column label="" prop="user"></el-table-column>
        <el-table-column label="" prop="telephone"></el-table-column>
        <el-table-column label="" prop="region"></el-table-column>
        <el-table-column label="" prop="date1"></el-table-column>
        <el-table-column label="" prop="date2"></el-table-column>
        <el-table-column label="" prop="type"></el-table-column>
        <el-table-column label="" prop="introduction"></el-table-column>
        <el-table-column label="" prop="status">
             <template slot-scope="scope">
        <el-tag
          :type="scope.row.status === '' ? 'danger' : 'success'"
          effect="dark"
          disable-transitions>{{scope.row.status}}</el-tag>
      </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tooltip
              effect="dark"
              content=""
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="showEditDialog(scope.row.user)"
              ></el-button>
            </el-tooltip>
            <el-tooltip
              effect="dark"
              content=""
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="removeRequestByUser(scope.row.user)"
              ></el-button>
            </el-tooltip>
            <el-tooltip
              effect="dark"
              content=""
              placement="top-start"
              :enterable="false"
            >
              <el-button
                type="info"
                icon="el-icon-s-claim"
                size="mini"
                @click="changeRequestByUser(scope.row.idrequest)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pageNum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <!--  -->
      <el-form
        ref="editFormRef"
        :model="editForm"
        :rules="formRules"
        label-width="80px"
      >
        <el-form-item label="" prop="name" required>
          <el-input v-model="editForm.name" style="width: 40%"></el-input>
        </el-form-item>
        <el-form-item label="" prop="user">
          <el-input
            v-model="editForm.user"
            style="width: 40%"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="telephone" required>
          <el-input v-model="editForm.telephone" style="width: 40%"></el-input>
        </el-form-item>
        <el-form-item label="" prop="region" required>
          <el-select v-model="editForm.region" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" required>
          <el-col :span="11">
            <el-form-item prop="date1">
              <el-date-picker
                type="date"
                placeholder=""
                v-model="editForm.date1"
                value-format="yyyy-MM-dd"
                style="width: 90%"
              ></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col class="line date_w" :span="2">-</el-col>
          <el-col :span="11">
            <el-form-item prop="date2">
              <el-time-picker
                placeholder=""
                v-model="editForm.date2"
                value-format="HH:mm:ss"
                style="width: 90%"
              ></el-time-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="type" required>
          <el-radio-group v-model="editForm.type">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="introduction" required>
          <el-input
            type="textarea"
            v-model="editForm.introduction"
            style="width: 40%"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editRequestInfo()"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="changeDialogVisible"
      width="30%"
    >
      <el-form         
        ref="changeFormRef"
        :model="changeForm"
        :rules="changeFormRules"
        label-width="80px">
        <el-form-item label="id">
          <el-input v-model="changeForm.idrequest" style="width: 40%" disabled></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="changeForm.status" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
      </el-form>
        <el-button @click="changeDialogVisible = false"> </el-button>
        <el-button type="primary" @click="changeHandle()"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  created() {
    this.getRequestList();
  },
  name: "RequestSecond",
  data() {
    return {
      // 
      queryInfo: {
        query: "",
        // 
        pageNum: 1,
        // 
        pageSize: 2,
      },
      requestList: [],
      total: 0,
      // /
      editDialogVisible: false,
      // 
      editForm: {
        name: "",
        user: "",
        telephone: "",
        region: "",
        date1: "",
        date2: "",
        type: "",
        introduction: "",
        status: "",
      },
      formRules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        user: [{ required: true, message: "", trigger: "blur" }],
        telephone: [
          { required: true, message: "", trigger: "blur" },
        ],
        region: [
          { required: true, message: "", trigger: "blur" },
        ],
        date1: [{ required: true, message: "", trigger: "blur" }],
        date2: [{ required: true, message: "", trigger: "blur" }],
        type: [{ required: true, message: "", trigger: "blur" }],
        introduction: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      // 
      changeDialogVisible:false,
      changeForm:{
        idrequest:"",
        status:""
      },
      changeFormRules:{},
    };
  },
  methods: {
    // 
    async getRequestList() {
      const { data: res } = await this.$http.get("/Home/requestSecond", {
        params: this.queryInfo,
      });
      if (res.meta.status !== 200)
        return this.$message.error("");
      this.requestList = res.data;
      this.total = res.total;
    },
    // pageSize
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize;
      this.getRequestList();
    },
    // pageNum
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage;
      this.getRequestList();
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields();
    },
    // 
    async showEditDialog(user) {
      this.editDialogVisible = true;
      console.log(user);
      const { data: res } = await this.$http.get("/Home/requestSecond/" + user);
      if (!res.meta.status == 200)
        return this.$message.error("");
      this.editForm = res.data[0];
    },
    // 
    editRequestInfo() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return;
        // 
        const { data: res } = await this.$http.put(
          "/Home/requestSecond/" + this.editForm.user,
          {
            name: this.editForm.name,
            user: this.editForm.user,
            telephone: this.editForm.telephone,
            region: this.editForm.region,
            date1: this.editForm.date1,
            date2: this.editForm.date2,
            type: this.editForm.type,
            introduction: this.editForm.introduction,
          }
        );
        if (res.meta.status !== 200)
          return this.$message.error("");
        this.$message.success("");
      });
      this.editDialogVisible = false;
      this.getRequestList();
    },
    // 
    async removeRequestByUser(user) {
      // 
      const confirmResult = await this.$confirm(
        ", ?",
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        }
      ).catch((err) => err);
      if (confirmResult !== "confirm") {
        return this.$message.info("");
      }
      const { data: res } = await this.$http.delete(
        "/Home/requestSecond/" + user
      );
      if (res.meta.status !== 200) return this.$message.error("");
      this.$message.success("");
      this.getRequestList();
    },
    // 
    async changeRequestByUser(id) {
      this.changeDialogVisible=true
      const {data:res} = await this.$http.get("/Home/requestSecond/change/"+id)
      if (res.meta.status !==200) return this.$message.error("")
      this.changeForm=res.data[0]
    },
    async changeHandle(){
      const{data:res} = await this.$http.post("/Home/requestSecond/change",this.changeForm)
      if (res.meta.status !==200) return this.$message.error("")
      this.$message.success("")
      this.changeDialogVisible=false
      this.getRequestList();
    }
  },
};
</script>

<style scoped></style>
